import {customElement} from '@here/hds-base';
import {List} from '@here/hds-list';
import {html, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {VerticalNavigationSubListItem} from './hds-vertical-navigation-sub-list-item.js';
import {styles} from './hds-vertical-navigation-sub-list.css.js';

/**
 * @slot default Content for the list of `hds-vertical-navigation-sub-list-item` elements
 */
@customElement('hds-vertical-navigation-sub-list')
export class VerticalNavigationSubList extends List {
  @property({type: Boolean, attribute: false}) isMainSubList = false;
  @query('slot:not([name])') protected slotElement!: HTMLSlotElement;

  static get styles(): any {
    return [super.styles, styles];
  }

  protected updated(_changedProperties: PropertyValues) {
    super.updated(_changedProperties);
  }

  protected firstUpdated() {
    this._slotChange();
  }

  private _slotChange() {
    this.isMainSubList =
      !(this.parentNode instanceof VerticalNavigationSubList) &&
      !(this.parentNode instanceof VerticalNavigationSubListItem);
  }

  protected render() {
    const classes = {
      '-hds-vertical-navigation-sub-list': true,
      'main-sub-list': this.isMainSubList,
    };

    return html`
      <div class="${classMap(classes)}" @slotchange="${this._slotChange}">${super.render()}</div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-vertical-navigation-sub-list': VerticalNavigationSubList;
  }
}
